import { createStore } from "vuex";
import actions from "./actions";
import mutations from "./mutations";

export interface State {
  scenicSpot: any;
}

export default createStore<State>({
  state: {
    scenicSpot: {},
  },
  getters: {
    scenicSpot(state) {
      return state.scenicSpot;
    },
  },
  mutations,
  actions,
  modules: {},
});
