import axios from "axios";
import jsSHA from "jssha";
import { State } from "./index";

interface APIHeaders {
  headers: {
    Authorization: string;
    "X-Date": string;
  };
}

function getAPIHeaders(): APIHeaders {
  const AppID = "8c62ce33fcdc4ef48048f66d78608de1";
  const AppKey = "Q8ImsvuvjN9dUpCWt9m6Gl4jb54";
  const GMTString = new Date().toUTCString();
  const ShaObj = new jsSHA("SHA-1", "TEXT");
  ShaObj.setHMACKey(AppKey, "TEXT");
  ShaObj.update("x-date: " + GMTString);
  const HMAC = ShaObj.getHMAC("B64");
  const Authorization = `hmac username="${AppID}", algorithm="hmac-sha1", headers="x-date", signature="${HMAC}"`;
  return {
    headers: {
      Authorization: Authorization,
      "X-Date": GMTString,
    },
  };
}

export default {
  async getScenicSpot(state: State): Promise<any> {
    const url =
      "https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$top=30&$format=JSON";
    axios
      .get(url, getAPIHeaders())
      .then((response: any) => {
        state.scenicSpot = response.data;
        console.log("response", response.data);
      })
      .catch((error: any) => {
        console.log(error);
      });
  },
};
